.github-profile {
    margin: 1rem;
  
    img {
      width: 75px;
      vertical-align: middle;
      border-style: none;
    }
  
    .info {
      display: inline-block;
      margin-left: 12px;
  
      .name {
        font-size: 1.25rem;
        font-weight: bold;
      }
    }
  }
  
  form {
    border: thin solid #ddd;
    background-color: #fbfbfb;
    padding: 2rem;
    margin-bottom: 2rem;
    display: flex;
    justify-content: center;
  
    .add-card-btn {
      background: #F21B3F;
      margin: 0 0 0 12px;
    }
  }
  
  .header {
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }